* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

.navbar {
  display: flex;
  height: 80px;
  background-color: #1b1b1b;
}

.navlists {
  list-style: none;
  display: flex;
  align-items: center;
  /* justify-content: center; */
  margin: 0 40px;
  gap: 20px;
}

.navitems {
  font-size: 1.3rem;
  font-family: sans-serif;
  color: white;
  cursor: pointer;
}

.navitems:hover {
  color: yellow;
  text-decoration: underline;
}

.navitems:active {
  color: blue;
}

.input__container {
  width: 50%;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 40px;
}

input {
  width: 70%;
  padding: 10px 20px;
  border-radius: 10px;
  outline: none;
  border: none;
  background-color: #8f8a8a;
  font-size: 1.3rem;
  color: white;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  caret-color: yellow;
  font-family: sans-serif;
  transition: all 300ms ease;
}

input:focus {
  transform: scale(1.1);
}
.btn {
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  font-size: 1.2rem;
  font-family: sans-serif;
  margin-left: 10px;
  padding: 10px 20px;
  border-radius: 10px;
  border: none;
  outline: none;
  cursor: pointer;
  transition: all 300ms ease-in-out;
}

.btn:hover {
  color: white;
  background-color: #8f8a8a;
}

.head {
  text-align: center;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  font-size: 3rem;
  margin: 40px 0;
}
.img__container {
  width: auto;
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  flex-wrap: wrap;
  margin: 40px 40px;
}

.img {
  width: 300px;
  height: 450px;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px,
    rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
  border-radius: 20px;
  background: linear-gradient(45deg, black, white);
}

img {
  width: 100%;
  height: 100%;
  border-radius: 20px;
  object-fit: cover;
}

.credit {
  font-size: 10px;
  text-align: center;
}
